import { ADD_FAVORITE, CANCEL_FAVORITE, JOB_FEATURED_LIST } from "@/api/api";
export default {
  name: "jobsListComponent",
  props: ['jobListData', 'adsData', 'jobPage', 'jobLimit', 'jobTotalNum', 'selectedJobId', 'isOther', 'companyInfo', 'fromBusinessProfile'],

  data() {
    return {
      jobHeight: 300,
      jobFeaturedData: []
    };
  },

  mounted() {// this.getJobFeaturedList()
  },

  methods: {
    exitPreview() {
      this.$router.push('/account/home');
    },

    jobPageSizeChange() {
      console.log('job page size');
      this.$emit('jobPageSize');
    },

    jobPageChange(e) {
      console.log('job page');

      if (this.isOther) {
        this.$router.push({
          path: '/jobs',
          query: {
            id: this.selectedJobId,
            page: e,
            from: 1
          }
        });
      } else {
        this.$router.push({
          path: '/jobs',
          query: {
            id: this.selectedJobId,
            page: e
          }
        });
      }

      this.$emit('jobPageChange', e);
    },

    addFavorite(id, type, title, url, index) {
      this.$emit('addFavorite', id, type, title, url, index);
    },

    cancelFavorite(type, typeId, index) {
      this.$emit('cancelFavorite', type, typeId, index);
    },

    backToSearchResults() {
      this.$emit('backToResults');
    },

    turnJobDetail(id) {
      this.$emit('jobDetailEvent', id, this.jobPage, this.isOther); // <router-link :to="{'path':'/jobs',query:{id:item.id,page:jobPage}}">
      // this.$router.push({path:'/jobs',query:{id:id,page:this.jobPage}})
    },

    getJobFeaturedList() {
      let params = {
        ad_type: 2
      };
      JOB_FEATURED_LIST(params).then(res => {
        console.log(res);

        if (res.code === 200) {
          this.jobFeaturedData = res.message;
          let jobDiv = document.getElementById('job'); // console.log(jobDiv.offsetHeight)

          this.jobHeight = jobDiv.offsetHeight;
        } else {
          console.log(res.msg);
        }
      }).catch(err => {
        console.log(err);
      });
    },

    addFavoriteForFeatured(id, type, title, url, index) {
      let params = {
        token: localStorage.getItem('token'),
        type: type,
        type_id: id,
        type_title: title,
        type_url: url
      };
      ADD_FAVORITE(params).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.$message.success('Success');
          this.jobFeaturedData[index]['is_favorite'] = 1;
        }
      }).catch(err => {
        console.log(err);
        this.$message.error(err.msg);
      });
    },

    cancelFavoriteForFeatured(type, typeId, index) {
      let params = {
        token: localStorage.getItem('token'),
        type: type,
        type_id: typeId
      };
      CANCEL_FAVORITE(params).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.jobFeaturedData[index]['is_favorite'] = 0;
        }
      }).catch(err => {
        console.log(err);
        this.$message.error(err.msg);
      });
    },

    turnAdsDetail(link) {
      window.open(link, '_blank');
    }

  }
};